#resultTable table.rs > tr > td:nth-of-type(3):before { font-weight:bold; content: ""; }
#resultTable table.rs > tr > td:nth-of-type(4):before { font-weight:bold; content: ""; }
#resultTable table.rs > tr > td:nth-of-type(5):before { font-weight:bold; content: ""; }
#resultTable table.rs > tr > td:nth-of-type(6):before { font-weight:bold; content: ""; }

#resultTable table.rs > tr > td:nth-of-type(7) { display:none; }
}*/

#modal_pw { 
		display: block;
		width: 100%;
		padding: 6px 12px;
		font-size: 14px;
		height: 36px;
		margin-bottom: 0.5rem;}


</style>